import React from "react";
import "../styles/webex.css";

function Webex() {
  return (
    <div className="webex-conainer">
      <header className="webex-header">Headline 30 characters max</header>
      <div className="webex-body">
        <p className="webex-content">
          Body copy 360 characters max, 70 character line limit. Give your bank
          an edge that other XDR solutions just can’t deliver. Switch on SecureX
          and access tools that will elevate your security.
        </p>
        <button className="webex-button">Sign up free</button>
      </div>
    </div>
  );
}

export default Webex;



